<template>
  <div>
    <!-- 导航栏 -->
    <navbar></navbar>
<!-- 注册 -->
<div class="register-container">
    <el-form ref="registerForm" :model="registerForm" :rules="registerFormRules" label-width="100px">
      <el-form-item label="用户名" prop="username">
        <el-input v-model="registerForm.username"></el-input>
      </el-form-item>
      <el-form-item label="邮箱" prop="email">
        <el-input v-model="registerForm.email"></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="password">
        <el-input type="password" v-model="registerForm.password"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="register">注册</el-button>
      </el-form-item>
    </el-form>
  </div>
</div>
</template>

<script>
import { MessageBox } from 'element-ui'
import Navbar from '@/components/NavbarHeader.vue'
export default {
  components: { Navbar },
  name: 'LoginZc',
  data () {
    return {
      registerForm: {
        username: '',
        email: '',
        password: ''
      },
      registerFormRules: {
        username: [{ required: true, message: '请输入用户名', trigger: 'blur' }],
        email: [
          { required: true, message: '请输入邮箱', trigger: 'blur' },
          { type: 'email', message: '请输入有效的邮箱地址', trigger: ['blur', 'change'] }
        ],
        password: [{ required: true, message: '请输入密码', trigger: 'blur' }]
      }
    }
  },
  methods: {
    register () {
      // 在登录逻辑中判断用户名和密码是否正确，并根据结果弹出提示框

      // 假设登录成功时，返回的结果为true
      if (this.registerForm.username === '111' && this.registerForm.email === '111@qq.com' && this.registerForm.password === '111') {
        MessageBox.alert('注册成功', '提示', {
          confirmButtonText: '确定',
          type: 'success',
          callback: () => {
            this.$router.push('/usercenter')// 跳转个人中心
          }
        })
      } else {
        MessageBox.alert('不能为空', '提示', {
          confirmButtonText: '确定',
          type: 'error'
        })
      }
    }
  }
}
</script>

<style>
.register-container {
    display: flex;
    flex-direction: column;
    align-items: center; /* 水平居中 */
    justify-content: center; /* 垂直居中 */
    height: 100vh; /* 设置容器的高度为视口高度，使内容垂直居中 */
  }
</style>
