<!-- Login.vue -->
<template>
  <div>
    <!-- 导航栏 -->
<navbar></navbar>
    <!-- 登录 -->
    <div class="login-container">
    <el-form ref="loginForm" :model="loginForm" :rules="loginFormRules" label-width="100px">
      <el-form-item label="用户名" prop="username">
        <el-input v-model="loginForm.username"></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="password">
        <el-input type="password" v-model="loginForm.password"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="login">登录</el-button>
        <el-button><router-link to="/register">注册</router-link></el-button>
      </el-form-item>
    </el-form>
    <div>
    </div>
  </div>
</div>
</template>

<script>
import Navbar from '@/components/NavbarHeader.vue'
import { MessageBox } from 'element-ui'

export default {
  components: { Navbar },
  data () {
    return {
      loginForm: {
        username: '111',
        password: '111'
      },
      loginFormRules: {
        username: [{ required: true, message: '请输入用户名', trigger: 'blur' }],
        password: [{ required: true, message: '请输入密码', trigger: 'blur' }]
      }
    }
  },
  methods: {
    login () {
      // 在登录逻辑中判断用户名和密码是否正确，并根据结果弹出提示框

      // 假设登录成功时，返回的结果为true
      if (this.loginForm.username === '111' && this.loginForm.password === '111') {
        MessageBox.alert('登录成功', '提示', {
          confirmButtonText: '确定',
          type: 'success',
          callback: () => {
            this.$router.push('/usercenter')// 跳转个人中心
          }
        })
      } else {
        MessageBox.alert('用户名或密码错误', '提示', {
          confirmButtonText: '确定',
          type: 'error'
        })
      }
    }
  }

}
</script>

<style>
.login-container {
  display: flex;
  flex-direction: column;
  align-items: center; /* 水平居中 */
  justify-content: center; /* 垂直居中 */
  height: 100vh; /* 设置容器的高度为视口高度，使内容垂直居中 */
}
</style>
