import Vue from 'vue'
import VueRouter from 'vue-router'
import HomePage from '@/views/HomePage'
import LoginDl from '@/views/LoginDl.vue'
import LoginZc from '@/views/LoginZc'
import FoodSafe from '@/views/FoodSafe'
import UserCenter from '@/views/UserCenter'
import UserUp from '@/views/UserUp'
import FoodSanumber from '@/views/FoodSanumber.vue'
import FoodSaknow from '@/views/FoodSaknow.vue'
import ArticleKnow from '@/views/Article/ArticleKnow.vue'

import NewArt from '@/components/Home/NewArt.vue'
import FoodBuy from '@/components/FoodBuy.vue'
Vue.use(VueRouter)

const routes = [
  {
    path: '/article/:category/:articleId',
    name: 'Article',
    component: ArticleKnow
  },
  { path: '/', component: HomePage },
  { path: '/login', component: LoginDl },
  { path: '/register', component: LoginZc },
  { path: '/foodsafe', component: FoodSafe },
  { path: '/usercenter', component: UserCenter },
  { path: '/userup', component: UserUp },
  { path: '/foodsafenum', component: FoodSanumber },
  { path: '/foodsaknow', component: FoodSaknow },
  { path: '/foodbuy', component: FoodBuy },

  {
    path: '/newaart/:id',
    name: 'NewArt',
    component: NewArt
  }
]

const router = new VueRouter({
  mode: 'hash', // 将 mode 设置为 'hash'
  base: process.env.BASE_URL,
  routes
})

export default router
