<template>
    <div>
        <navbar></navbar>
        <div class="newcontant">
          <h3>{{ news.title }}</h3>
          <p>{{ news.content }}</p>
        </div>

      </div>
</template>

<script>
import Navbar from '@/components/NavbarHeader.vue'
export default {
  components: { Navbar },
  data () {
    return {
      news: {},
      newsList: [
        { id: 1, title: '新闻标题1', content: '新闻内容1' },
        { id: 2, title: '新闻标题2', content: '新闻内容2' },
        { id: 3, title: '新闻标题3', content: '新闻内容3' },
        { id: 4, title: '新闻标题4', content: '新闻内容4' }
        // 更多新闻...
      ]
    }
  },
  methods: {
    getNewsById (id) {
      return this.newsList.find(news => news.id === parseInt(id)) || { title: 'Not Found', content: 'The news you are looking for is not found.' }
    }
  },
  watch: {
    '$route' (to, from) {
      const id = to.params.id
      this.news = this.getNewsById(id)
    }
  },
  mounted () {
    const id = this.$route.params.id
    if (id) {
      this.news = this.getNewsById(id)
    } else {
      this.news = this.newsList[0]
    }
  }
}
</script>

<style>
.newcontant {
  margin: 0 100px 0 100px;
}
</style>
