<template>
    <el-card>
      <el-menu @select="handleMenuSelect">
        <el-menu-item index="poultry">家禽食品安全知识</el-menu-item>
        <el-menu-item index="fruits">水果蔬菜食品安全知识</el-menu-item>
        <el-menu-item index="seafood">海鲜食品安全知识</el-menu-item>
        <el-menu-item index="dairy">乳制品食品安全知识</el-menu-item>
        <el-menu-item index="grains">谷物食品安全知识</el-menu-item>
        <el-menu-item index="meat">肉类食品安全知识</el-menu-item>
        <el-menu-item index="eggs">蛋类食品安全知识</el-menu-item>
        <el-menu-item index="vegetables">蔬菜食品安全知识</el-menu-item>
        <el-menu-item index="nuts">坚果食品安全知识</el-menu-item>
        <el-menu-item index="processed">加工食品安全知识</el-menu-item>
      </el-menu>
    </el-card>
  </template>

<script>
export default {
  created () {
    // 默认选中第一个菜单项，显示家禽食品安全知识
    this.handleMenuSelect('poultry')
  },
  methods: {
    handleMenuSelect (index) {
      const article = this.getArticleByIndex(index) // 根据index找到对应的文章
      this.$emit('articleSelected', article) // 将选中的文章传递给父组件
    },
    getArticleByIndex (index) {
      // 根据index找到对应的文章并返回
      // 可以根据具体情况从数据源中获取，这里只是示例
      if (index === 'poultry') {
        return { id: 1, title: '家禽食品安全知识', content: '在食用家禽时，请确保烹饪至适当温度以杀死任何有害细菌。避免生食或生熟不均的情况。' }
      } else if (index === 'fruits') {
        return { id: 2, title: '水果蔬菜食品安全知识', content: '在选购水果和蔬菜时，要注意清洗彻底，以去除表面的农药残留和细菌。定期清洗冰箱以防止交叉污染。' }
      } else if (index === 'seafood') {
        return { id: 3, title: '海鲜食品安全知识', content: '购买海鲜时，确保选购新鲜的产品。烹饪前检查是否有任何异味或变质迹象。避免在存放时间过长后食用。' }
      } else if (index === 'dairy') {
        return { id: 4, title: '乳制品食品安全知识', content: '存放奶制品时要保持冷藏，并在开封后尽快食用。观察保质期并避免过期食用。' }
      } else if (index === 'grains') {
        return { id: 5, title: '谷物食品安全知识', content: '存放谷物时要保持干燥，并注意有无虫蛀迹象。购买有机谷物可以减少农药残留的风险。' }
      } else if (index === 'meat') {
        return { id: 6, title: '肉类食品安全知识', content: '在购买肉类时，要选择新鲜的并在烹饪前彻底加热。避免将生肉与其他食材混合，以防止交叉污染。' }
      } else if (index === 'eggs') {
        return { id: 7, title: '蛋类食品安全知识', content: '购买鸡蛋时要检查有无裂纹，并在烹饪前洗净蛋壳。煮熟的鸡蛋可以避免食源性疾病的风险。' }
      } else if (index === 'vegetables') {
        return { id: 8, title: '蔬菜食品安全知识', content: '选择新鲜蔬菜并在烹饪前充分清洗。注意在储存过程中避免受潮以及与生肉或其他生食品接触。' }
      } else if (index === 'nuts') {
        return { id: 9, title: '坚果食品安全知识', content: '储存坚果时要避免高温和潮湿环境，以防止霉菌生长。购买时检查是否有虫蛀迹象，并避免过量食用。' }
      } else if (index === 'processed') {
        return { id: 10, title: '加工食品安全知识', content: '购买加工食品时要阅读标签，避免食用添加剂过多或人工合成色素。注意存放温度以及保质期。' }
      }
    }
  }
}
</script>

